.skills {
  padding-block: 60px;

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 120px;
  }

  &__hard {
    &-title {
      font-size: 40px;
      font-weight: 400;
      margin-bottom: 20px;
    }
  }

  &__soft {
    &-title {
      font-size: 40px;
      font-weight: 400;
      margin-bottom: 20px;
    }
  }

  &__list-item {
    display: flex;
    align-items: center;
    justify-content: start;
    min-height: 70px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: var(--box-shadow-small);
    font-size: 20px;
    transition: all 0.3s;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &:hover {
      box-shadow: var(--box-shadow-inset);
      color: var(--color-accent);
    }
  }
}
