.hero {
  padding-block: 50px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__body {
    max-width: 55%;
  }

  &__descr {
    margin-bottom: 40px;
    font-size: 45px;
    font-weight: 400;
    text-wrap: nowrap;

    &::first-line {
      color: var(--color-accent);
    }
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 190px;
    padding: 10px;
    font-size: 22px;
    border-radius: 20px;
    box-shadow: var(--box-shadow-small);

    &:hover {
      border: 1px solid var(--color-accent);
    }
  }

  @keyframes animate {
    0%,
    100% {
      opacity: 1;
    }

    50% {
      opacity: 0;
    }
  }

  &--anim {
    position: relative;
    animation: animate 5s infinite;
  }

  &__scene {
    max-width: 50%;
  }
}
