.header {
  height: 70px;

  &__logo {
    width: 15%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
  }

  &__container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__nav {
    margin-left: -9%;
  }

  &__nav-list {
    display: flex;
  }

  &__nav-item {
    font-size: 25px;
    font-weight: 400;

    &:not(:last-child) {
      margin-right: 25px;
    }
  }
}
