:root {
  //colors

  --color-light: #f5f5f5;
  --color-dark: #1a1a1d;
  --color-accent: #904e95;
  --color-alt-dark: #1d1b1b;
  --c-dark-for-shadow: #000;

  //light-mode

  --color-theme: #1a2a1d;

  //
  --transition-duration: 0.3s;

  //shadows

  --box-shadow-small: -1px -1px 7px hsla(0, 0%, 100%, 0.12),
    4px 4px 5px rgba(0, 0, 0, 0.5);
  --box-shadow-inset: 0px 0px 25px 9px rgba(0, 0, 0, 0.64) inset;
}
